
.App {
  text-align: center;
}

.App-logo {
  width: 18vmin;
  height: 18vmin;
  border-radius: 50%;
  border: .34vmin solid #61DAFB;
  /* filter: drop-shadow(.5vmin .5vmin 0px #61DAFB); */
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header h1 {
  color: white;
  margin-top: 1vmin;
  margin-bottom: 1vmin;
  margin-left: 1em;
  margin-right: 1em;
  font-size: calc(8.5vmin);
  color: white;
}

.social-links {
  list-style-type: none;
  margin-top: 1em;
  margin-bottom: 1em;
  padding: 0;
}

.social-links li {
  font-size: calc(3vmin);
  display: inline;
  margin-right: 0.5em;
  margin-left: 0.5em;
}

.social-link {
  color: #61dafb;
}

hr.fancy-hr {
  width: 25%;
  border: 0;
  height: 2px;
  margin-top: 3.5vmin;
  margin-bottom: 3.5vmin;
  background-image: linear-gradient(to right, rgba(97,218,251,0.1), rgba(197,218,251,0.3), rgba(97,218,251,0.1));
}

.copy {
  color: grey;
  font-size: calc(2vmin);
}

@media only screen and (max-width: 600px) {

  .App-logo {
    width: 40vmin;
    height: 40vmin;
  }

  .App-header h1 {
    font-size: calc(15.5vmin);
  }

  .social-links li {
    font-size: calc(4vmin);
  }

  hr.fancy-hr {
    width: 50%;
  }

  .copy {
    font-size: calc(4vmin);
  }

}
